.photos-head {
  position: relative;
  width: 320px;
  margin: 0;
  padding: 0;
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.3);
}

.photos-head__picture {
  position: absolute;
  top: 53px;
  left: 44px;
  width: 230px;
  height: 40px;
}

.photos-head--sedona::after {
  content: "";
  position: absolute;
  top: 132px;
  left: 0.1px;
  width: 322px;
  height: 26px;
  background-repeat: no-repeat;
  background-image: url("../img/bg-intro-triangle-mobile.svg");
}

.photos-head__title {
  display: block;
  margin-top: 20px;
  padding: 20px;
  padding-bottom: 9px;
  font-size: @medium-font;
  font-weight: 700;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}

.photos-head__text {
  margin-bottom: 22px;
  font-size: @little-font;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  color: #333333;
}

@media (min-width: @tablet-width) {
  .photos-head {
    width: 768px;
    min-height: 513px;
    border-bottom: none;
  }

  .photos-head__picture {
    position: absolute;
    top: 94px;
    left: 155px;
    width: 460px;
    height: 80px;
  }

  .photos-head--sedona::after {
    content: "";
    position: absolute;
    top: 234px;
    left: 1px;
    width: 768px;
    height: 57px;
    background-repeat: no-repeat;
    background-image: url("../img/bg-intro-triangle-tablet.svg");
  }

  .photos-head__title {
    font-size: 30px;
    margin-top: 55px;
    margin-bottom: 20px;
  }

  .photos-head__text br {
    display: none;
    width: 428px;
  }

  .photos-head__text {
    width: 428px;
    margin: 0 auto 10px;
  }
}

@media (min-width: @desktop-width) {
  .photos-head {
    width: 1200px;
  }

  .photos-head__position {
    position: relative;
  }

  .photos-head__picture {
    position: absolute;
    top: 100px;
    left: 370px;
    width: 460px;
    height: 80px;
  }

  .photos-head--sedona::after {
    content: "";
    position: absolute;
    top: 244px;
    left: 0.3px;
    width: 1200px;
    height: 57px;
    background-repeat: no-repeat;
    background-image: url("../img/bg-intro-triangle-desktop.svg");
  }

  .photos-head__title {
    margin-top: 91px;
    margin-bottom: 22px;
  }

  .photos-head__text {
    margin-bottom: 68px;
  }
}
