.button-form {
  display: block;
  width: 280px;
  height: 58px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 48px;
  margin-bottom: 43px;
  padding-right: 21px;
  font-size: @large-font;
  font-weight: 700;
  line-height: 58px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: @blue;
}

@media (min-width: @tablet-width) {
  .button-form {
    width: 324px;
    height: 58px;
    margin: 28px 1px 60px 1px;
    font-size: @large-font;
  }
}

@media (min-width: @desktop-width) {
  .button-form {
    width: 480px;
    height: 58px;
    font-size: @large-font;
  }
}
