.advantages {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: flex-start;
  .list-reset();
  padding-top: 35px;
  padding-left: 70px;
  padding-bottom: 10px;
}

.advantages__home {
  display: block;
  margin: 0;
  margin-bottom: 29px;
  padding: 0;
  width: 210px;
  text-align: left;
}

.advantages__title {
  display: block;
  margin-bottom: 13px;
  font-size: @medium-font;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
}

.advantages__description {
  display: block;
  margin: 0;
  padding: 0;
  font-size: @little-font;
  font-weight: 400;
  line-height: 21px;
  text-align: left;
  text-transform: uppercase;
  color: #333333;
}

.advantages__souvenires {
  display: block;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  width: 210px;
  text-align: left;
}

.advantages__description1 {
  display: block;
  margin: 0;
  padding: 0;
  font-size: @little-font;
  font-weight: 400;
  line-height: 21px;
  text-align: left;
  text-transform: uppercase;
  color: #333333;
}

.advantages__description1-text1 {
  display: inline-block;
  margin: 0;
  padding: 0;
}

.advantages__description1-text2 {
  display: inline-block;
  margin: 0;
  padding: 0;
}

.advantages__description1-text3 {
  display: inline-block;
  margin: 0;
  padding: 0;
}

.advantages__description1-text4 {
  display: inline-block;
  margin: 0;
  padding: 0;
}

.advantages__meal {
  display: block;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  width: 210px;
  text-align: left;
}

.advantages__home--home::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 23px;
  width: 35px;
  height: 36px;
  background-repeat: no-repeat;
  background-image: url("../img/icon-house37x36.svg");
}

.advantages__souvenires--souvenires::after {
  content: "";
  position: absolute;
  top: 154px;
  left: 25px;
  width: 32px;
  height: 38px;
  background-repeat: no-repeat;
  background-image: url("../img/icon-gift32x38.svg");
}

.advantages__meal--meal::after {
  content: "";
  position: absolute;
  top: 270px;
  left: 19px;
  width: 40px;
  height: 35px;
  background-repeat: no-repeat;
  background-image: url("../img/icon-burger40x35.svg");
}

@media (min-width: @tablet-width) {
  .advantages {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 768px;
    min-height: 310px;
    margin: 0;
    padding: 158px 47px 71px;
  }

  .advantages__home {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 156px;
    height: 100px;
    margin: 0;
    margin-left: 3px;
    padding: 0;
  }

  .advantages__souvenires {
    order: 3;
    width: 163px;
    height: 100px;
    margin: 0;
    padding: 0;
  }

  .advantages__meal {
    width: 150px;
    height: 100px;
    margin: 0;
    margin-left: 5px;
    padding: 0;
  }

  .advantages__meal br {
    display: none;
  }

  .advantages__title {
    display: block;
    margin: 0;
    margin-bottom: 20px;
    padding: 0;
    font-size: @large-font;
    text-align: center;
  }

  .advantages__description {
    display: inline-block;
    margin: 0;
    padding: 0;
    font-size: @little-font;
    text-align: center;
  }

  .advantages__description br {
    display: none;
  }

  .advantages__description1-text2 {
    display: inline-block;
    margin: 0;
    margin-left: 28px;
    padding: 0;
  }

  .advantages__description1-text4 {
    display: inline-block;
    margin: 0;
    margin-left: 27px;
    padding: 0;
  }

  .advantages__home--home::after {
    content: "";
    position: absolute;
    top: 59px;
    left: 91px;
    width: 75px;
    height: 72px;
    background-repeat: no-repeat;
    background-image: url("../img/icon-house.svg");
  }

  .advantages__souvenires--souvenires::after {
    content: "";
    position: absolute;
    top: 57px;
    left: 608px;
    width: 77px;
    height: 75px;
    background-repeat: no-repeat;
    background-image: url("../img/icon-gift.svg");
  }

  .advantages__meal--meal::after {
    content: "";
    position: absolute;
    top: 61px;
    left: 347px;
    width: 77px;
    height: 75px;
    padding-bottom: 10px;
    background-repeat: no-repeat;
    background-image: url("../img/icon-burger.svg");
  }
}

